* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

li {
  display: flex;
  align-items: center;
  list-style: none;
  margin-bottom: 5px;
}

li:last-child{
  margin-bottom: 0;
}

li img {
  margin-right: 10px;
}

.one{
  flex-direction: column;
}

.img--big {
  width: 150px;
  height: auto;  
}

.result-country{
  font-size: 1.5rem;
  font-weight: 700;
}